import { ReactElement, JSXElementConstructor, ReactFragment, ReactPortal } from "react"
import Marquee from "react-fast-marquee"

import { HashLink as Link} from 'react-router-hash-link'
import "./courses.css"
function COURSECard(props: { marquee :any,  title: string | number | boolean | ReactElement<any, string | JSXElementConstructor<any>> | ReactFragment | ReactPortal | null | undefined , img: string | undefined }) {
  return(
    
<div className="card" >
 
 { <Marquee style = {{color:"darkred"}} speed={78} pauseOnHover gradient={false} >Addmission Open!</Marquee> }
 
  <div className="card-body">
    
    <img  src={props.img}/>
    <h2 className="card-title" >{props.title}</h2>
    
  </div>
  <Link to="#FORM"  onClick={showADDMISION} smooth><button  className="card-btn" >APPLY</button></Link>
</div>

  )
}
function showADDMISION(){
  document.getElementById("CONTACT")!.style.marginTop = "600px"
  document.getElementById("FORM")!.style.display = "block"
  document.getElementById("ABOUT")!.style.marginTop = "670px"
}
export default COURSECard